import React, { Component } from 'react'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import { clearFix } from 'polished'
import styles from '../styles'
import { rhythm } from '../utils/typography'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/NavBar'
import Footer from '../components/footer'
import Tags from '../components/tags'
import Img from 'gatsby-image'

const PostTitle = styled.h1`
  text-align: left;
  font-weight: 700;
  margin-bottom: 0.5rem;
`

const PostSubTitle = styled.h2`
  margin-top: 0.5rem;
  font-size: 1.5rem;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.54);
`

const PostInfo = styled.div`
  ${clearFix()};
  font-size: 0.9rem;
`

const TagsWrapper = styled.div`
  margin-top: ${rhythm(1 / 2)};
`

const MainImageArea = styled.div`
  width: 100%;
  max-width: ${styles.sizes.largeDesktop};
  margin: ${rhythm(2)} auto 0;
`

const MainImageAlt = styled.em`
  font-size: 0.8rem;
  line-height: 1.4;
  display: block;
  width: calc(100% - ${rhythm(2)});
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: ${rhythm(1)};
  margin-bottom: ${rhythm(3)};
  color: hsla(291, 0%, 18%, 0.5);
  & a {
    color: hsla(291, 0%, 18%, 0.5);
  }

  ${styles.media.Desktop} {
    width: 90%;
  }
`

const Post = styled.article`
  margin-top: ${rhythm(2)};
  margin-bottom: ${rhythm(2)};
`

class BlogPostRoute extends Component {
  getScriptSrc() {
    const scripts = []
    const scriptRegex = /<script[^>].*<\/script>/gi
    let match = []

    // get script tags
    while (match) {
      match = scriptRegex.exec(this.props.data.markdownRemark.html)
      if (match) {
        scripts.push(match[0])
      } else {
        break
      }
    }

    // parse src from script tags
    return scripts.map(script => {
      return /(src=)"(.+)"/.exec(script)[2]
    })
  }

  render() {
    const post = this.props.data.markdownRemark
    const { frontmatter } = post
    const siteMetadata = this.props.data.site.siteMetadata
    const title = `${frontmatter.title} · ${siteMetadata.title}`

    return (
      <PageWrapper>
        <Helmet
          title={title}
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}${this.props.location.pathname}`,
            },
          ]}
          meta={[
            { name: 'author', content: `${siteMetadata.author}` },
            { name: 'keywords', content: `${frontmatter.tags.join(`,`)}` },
            {
              name: 'description',
              content:
                frontmatter.description || frontmatter.subTitle || frontmatter.title,
            },
            { property: 'og:type', content: 'article' },
            {
              property: 'og:url',
              content: `${siteMetadata.url}${this.props.location.pathname}`,
            },
            { property: 'og:title', content: title },
            {
              property: 'og:description',
              content: `${frontmatter.description || title}`,
            },
            {
              property: 'og:image',
              content: `${frontmatter.mainImage || '/logos/logo-192x192.png'}`,
            },
          ]}
        >
          {this.getScriptSrc().map((src, i) => (
            <script async src={src} type="text/javascript" key={i} />
          ))}
          {/* Disqus 댓글
          https://rhostem.disqus.com/admin/settings/universalcode/*/}
          <script type="text/javascript">{`
            /**
             *  RECOMMENDED CONFIGURATION VARIABLES: EDIT AND UNCOMMENT THE SECTION BELOW TO INSERT DYNAMIC VALUES FROM YOUR PLATFORM OR CMS.
             *  LEARN WHY DEFINING THESE VARIABLES IS IMPORTANT: https://disqus.com/admin/universalcode/#configuration-variables
             */
            /*
            var disqus_config = function () {
                this.page.url = PAGE_URL;  // Replace PAGE_URL with your page's canonical URL variable
                this.page.identifier = PAGE_IDENTIFIER; // Replace PAGE_IDENTIFIER with your page's unique identifier variable
            };
            */
            (function() {  // DON'T EDIT BELOW THIS LINE
                var d = document, s = d.createElement('script');

                s.src = 'https://rhostem.disqus.com/embed.js';

                s.setAttribute('data-timestamp', +new Date());
                (d.head || d.body).appendChild(s);
            })();
          `}</script>
          <noscript>{`
            Please enable JavaScript to view the <a href="https://disqus.com/?ref_noscript" rel="nofollow">comments powered by Disqus.</a>
          `}</noscript>
        </Helmet>
        <NavBar />
        <PostContentWrapper>
          <header>
            <PostTitle>{frontmatter.title}</PostTitle>
            {frontmatter.subTitle && <PostSubTitle>{frontmatter.subTitle}</PostSubTitle>}
            <PostInfo>
              {frontmatter.date} &middot; 읽기 {post.timeToRead}분
            </PostInfo>
            <TagsWrapper>
              <Tags tags={frontmatter.tags} />
            </TagsWrapper>
          </header>
        </PostContentWrapper>

        {frontmatter.mainImage && (
          <MainImageArea>
            <Img sizes={frontmatter.mainImage.childImageSharp.sizes} />

            {frontmatter.mainImageAlt && (
              <MainImageAlt>{frontmatter.mainImageAlt}</MainImageAlt>
            )}
          </MainImageArea>
        )}
        <PostContentWrapper>
          {/* post는 utils/typograyph.js 에서 사용하는 클래스네임이다 */}
          <Post className="post" dangerouslySetInnerHTML={{ __html: post.html }} />
          <div id="disqus_thread" />
        </PostContentWrapper>
        <Footer />
      </PageWrapper>
    )
  }
}

export default BlogPostRoute

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        url
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      fields {
        tagSlugs
      }
      frontmatter {
        title
        subTitle
        tags
        date(formatString: "YYYY-MM-DD")
        mainImageAlt
        description
        mainImage {
          childImageSharp {
            sizes(maxWidth: 1600) {
              aspectRatio
              base64
              sizes
              src
              srcSet
            }
          }
        }
      }
    }
  }
`



